.hero__title {
  color: var(--accent-color);
  line-height: 1.25;
  @media screen and(min-width: 1440px) {
    font-size: 48px;
    line-height: 1.27;
    margin-bottom: 24px;
  }
  @media screen and(max-width: 1439px) {
    font-size: 36px;
    margin-bottom: 16px;
  }
  @media screen and(max-width:767px) {
    font-size: 24px;
    margin-bottom: 16px;
  }
}

.proccess_title {
  line-height: 1.25;
  color: var(--main-text-color);
  @media screen and(min-width: 1440px) {
    font-size: 18px;

    font-weight: 300;
    width: 515px;
  }

  @media screen and(max-width: 1439px) {
    font-size: 16px;

    font-weight: 300;
    width: 514px;
  }

  @media screen and(max-width:767px) {
    font-size: 16px;

    font-weight: 300;
    width: 100%;
  }
}
@media screen and (min-width: 1440px) {
  .proccess-list_item {
    height: 138px;
  }
}

.proccess-list_item:not(:last-child) {
  margin-bottom: 16px;

  @media screen and (min-width: 1440px) {
    margin-right: 32px;
  }
}
@media screen and (min-width: 768px) {
  .proccess-list_item:last-child {
    height: 100%;
  }
}
.scanning {
  padding-top: 175px;
  @media screen and (max-width: 320px) {
    padding-top: 93px;
  }
  @media screen and(max-width: 1439px) {
    padding-top: 105px;
  }
}
.section-scanning {
  padding-top: 24px;

  @media screen and (min-width: 768px) {
    padding-top: 52px;
  }
  @media screen and (min-width: 1440px) {
    padding-top: 0;
  }
}
@media screen and (min-width: 1440px) {
  .proccess_list {
    height: 400px;
    padding-bottom: 76px;
  }
}
.proccess-images {
  background-image: url('/images/mobile/3d-scanning/Scanning-main-min.jpg');

  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-repeat: no-repeat;

  @media screen and(max-width:767px) {
    background-position: bottom;
    background-size: 280px 199px;
    height: 735px;
  }
  @media screen and (min-width: 768px) {
    flex-direction: inherit;
    background-position: 100%;
    background-size: 232px 203px;
  }
  @media screen and (min-width: 768px) and (max-width: 1439px) {
    width: 112%;
  }
  @media screen and(min-width: 1440px) {
    flex-direction: column;
    background-size: 515px 368px;
    background-repeat: no-repeat;
    background-position: 100% 100%;
  }
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .proccess-images {
    background-image: url('/images/mobile/3d-scanning/Scanning-main@2x-min.jpg');
  }
}

@media screen and (min-width: 768px) and (min-device-pixel-ratio: 2),
  screen and (min-width: 768px) and (min-resolution: 192dpi),
  screen and (min-width: 768px) and (min-resolution: 2dppx) {
  .proccess-images {
    background-image: url('/images/tablet/3d-scanning/scanning_photo1@2x-min.jpg');
  }
}

@media screen and (min-width: 1440px) {
  .proccess-images {
    background-image: url('/images/desktop/3d-scanning/scanning-main-min.jpg ');
    // background-size: 1440px;
  }
}

@media screen and (min-width: 1440px) and (min-device-pixel-ratio: 2),
  screen and (min-width: 1440px) and (min-resolution: 192dpi),
  screen and (min-width: 1440px) and (min-resolution: 2dppx) {
  .proccess-images {
    background-image: url('/images/desktop/3d-scanning/scanning-main@2x-min.jpg');
  }
}

.scan_title {
  color: var(--accent-color);
  line-height: 1.25;
  @media screen and(min-width: 1440px) {
    font-size: 36px;

    margin-bottom: 52px;
  }
  @media screen and(max-width: 1439px) {
    font-size: 24px;
    margin-bottom: 24px;
  }
  @media screen and(max-width:767px) {
    font-size: 18px;
    margin-bottom: 16px;
    // text-align: center;
  }
}
.thumb {
  background: #ffffff;
  //   opacity: 0.8;
  box-shadow: inset 1px 1px 16px #cbcbcb, inset -1px -1px 16px #ffffff;
  border-radius: 50px 0px;

  @media screen and(min-width: 1440px) {
    width: 250px;
    height: 298px;
  }
  @media screen and(max-width: 1439px) {
    width: 153px;
    height: 210px;
  }
  @media screen and(max-width:767px) {
    width: 133px;
    height: 182.55px;
  }
}
.scanning_list {
  display: flex;
  width: 100%;
  @media screen and(min-width: 1440px) {
  }
  @media screen and (min-width: 768px) {
  }
  @media screen and(max-width:767px) {
    flex-wrap: wrap;
    justify-content: space-around;
  }
}

.scanning-list_item:not(:last-child) {
  margin-right: 0;

  @media screen and (min-width: 768px) {
    margin-right: 16px;
  }
}
.scanning-list_item {
  @media screen and(min-width: 1440px) {
    margin-bottom: 100px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 52px;
  }
  @media screen and(max-width:767px) {
    margin-bottom: 55px;
  }
}

.scanning-text {
  color: var(--accent-color);
  line-height: 1.285;
  text-align: center;
  letter-spacing: 0.03em;

  @media screen and(min-width: 1440px) {
    font-size: 18px;
    width: 248px;
    padding-top: 19px;
  }
  @media screen and(max-width: 1439px) {
    width: 121.26px;
    padding-left: 26px;
  }
  @media screen and(max-width:767px) {
    width: 131.26px;
    padding-left: 1px;
  }
}

.scanning-images {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
}
.scanning-button {
  width: 280px;
  height: 56px;
  align-items: center;
  padding: 16px 32px;
  background: #f5f5fa;
  border-radius: 30.5px;
  color: var(--accent-color);
  text-align: center;
  letter-spacing: 0.1em;
  // display: flex;
  margin-left: auto;
  margin-right: auto;
  border: #f5f5fa;
  box-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.1),
    5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
  display: block;

  &:hover {
    cursor: pointer;
  }
}
.scanning-section {
  padding-bottom: 76px;
}
