.contact-about {
  padding-top: 93px;
  padding-bottom: 52px;

  @media screen and (min-width: 768px) {
    padding-top: 105px;
  }

  @media screen and (min-width: 1440px) {
    padding-top: 175px;
    padding-bottom: 100px;
  }

  &__title {
    margin-bottom: 16px;

    font-weight: 700;
    font-size: 24px;
    line-height: 1.25;

    color: var(--accent-color);

    @media screen and (min-width: 768px) {
      margin-bottom: 24px;
      font-size: 36px;
      line-height: 45px;
    }
    @media screen and (min-width: 1440px) {
      font-size: 48px;
    }
  }
  &__content {
    @media screen and (min-width: 768px) {
      display: flex;
    }
  }

  &__video {
    display: block;
    margin: 0 auto;

    width: 280px;
    height: 171px;
    margin-bottom: 16px;
    border-radius: 15px;

    @media screen and (min-width: 768px) {
      width: 247px;
      height: 192px;

      margin-right: 20px;
      margin-bottom: 0;
    }

    @media screen and (min-width: 1440px) {
      min-width: 515px;
      height: 315px;

      margin-right: 32px;
    }
  }

  &__description {
    position: relative;
  }

  &__description-text {
    font-weight: 300;
    font-size: 16px;
    line-height: 1.25;

    color: var(--main-text-color);

    &:not(:last-child) {
      margin-bottom: 16px;
      @media screen and (min-width: 1440px) {
        margin-bottom: 24px;
      }
    }

    @media screen and (min-width: 1440px) {
      font-size: 18px;
      line-height: 1.27;
    }
  }
}

.emoji::after {
  position: absolute;
  display: inline-block;
  margin-left: 8px;
  width: 22px;
  height: 22px;

  content: '';
  background-image: url(../images/mobile/contact/emoji-min.png);
  background-size: cover;

  @media screen and (min-width: 768px) {
    margin-left: 18px;
  }

  @media screen and (min-width: 1440px) {
    display: block;
    margin-left: 0;
    width: 49px;
    height: 49px;
    background-image: url(../images/desktop/contact/emoji-min.png);
    bottom: 0;
  }
}

.contact-field {
  padding-bottom: 76px;

  &__container {
    display: flex;
    flex-direction: column-reverse;

    @media screen and (min-width: 1440px) {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__contact {
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: space-between;
    }
    @media screen and (min-width: 1440px) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__contact-item:not(:last-child) {
    margin-bottom: 24px;

    @media screen and (min-width: 1440px) {
      margin-bottom: 68px;
    }
  }

  &__contact-title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.27;
    color: var(--accent-color);

    &-tablet {
      @media screen and (min-width: 768px) and (max-width: 1439px) {
        text-align: center;
      }
    }
    @media screen and (min-width: 1440px) {
      font-size: 36px;
      line-height: 1.25;
    }
  }

  &__link {
    display: block;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: var(--main-text-color);

    @media screen and (min-width: 768px) {
      margin-bottom: 8px;
    }

    @media screen and (min-width: 1440px) {
      font-size: 18px;
      line-height: 1.11;
      margin-bottom: 18px;
    }
  }

  &__timetable:not(:last-child) {
    margin-bottom: 8px;
    @media screen and (min-width: 1440px) {
      margin-bottom: 18px;
    }
  }

  &__timetable {
    @media screen and (min-width: 768px) {
      text-align: right;
    }
    @media screen and (min-width: 1440px) {
      text-align: left;
    }
  }

  &__hours {
    display: block;
    font-size: 16px;
    line-height: 1.25;

    @media screen and (min-width: 1440px) {
      font-size: 18px;
      line-height: 1.11;
    }
  }

  ////FORM////

  &__form-container {
    margin-bottom: 52px;

    @media screen and (min-width: 768px) {
      display: block;
      margin: 0 auto;
      margin-bottom: 76px;
      width: 420px;
    }
    @media screen and (min-width: 1440px) {
      width: 516px;
      margin: 0;
    }
  }
  &__form {
    display: flex;
    flex-direction: column;
  }

  &__form-field {
    font-weight: normal;
    font-size: 14px;
    line-height: 1.43;
    color: var(--main-text-color);
  }

  &__form-input {
    width: 100%;
    height: 24px;
    margin-bottom: 44px;

    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: var(--main-text-color);

    outline: none;
    border: none;

    border-bottom: 1px solid var(--accent-color);

    &::placeholder {
      color: var(--main-text-color);
    }
  }

  &__message-container {
    position: relative;
  }

  &__figure {
    margin-top: 4px;
    width: 100%;

    height: 200px;
    border: 1px solid var(--accent-color);
    border-radius: 50px 0px;
  }

  &__form-message-field {
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: var(--main-text-color);
  }

  &__form-message {
    margin-top: 22px;
    margin-left: 20px;
    width: 90%;
    height: 60%;
    resize: none;
    border: none;
  }

  &__file-input {
    display: block;
    margin-top: 4px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.25;
    color: #cdcdcd;

    max-width: 228px;
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
      max-width: 390px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 100%;
    }
  }

  &__file-input label::before {
    content: '';
    display: block;
    position: absolute;
    right: 20px;
    top: 156px;

    width: 22px;
    height: 20px;
    fill: #cdcdcd;
    background-image: url(../images/clip.svg);
    cursor: pointer;
  }

  &__form-button {
    align-self: center;

    min-width: 280px;
    padding: 16px 83px;

    background: #f5f5fa;
    box-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.1),
      5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
    border-radius: 30.5px;
    border: none;
    cursor: pointer;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: var(--accent-color);

    @media screen and (min-width: 768px) {
      align-self: flex-end;

      min-width: 177px;
      padding: 16px 32px;
    }
  }
}
